html {
    min-height: 100vh;
    display: flex;
}

* {
    box-sizing: border-box;
}

body {
    display: flex;
    flex-grow: 1;
    min-height: 100vh;
}

#root {
    flex-direction: column;
    flex-grow: 1;
    min-height: 100vh;
    display: flex;
}

.page {
    flex-grow: 1;
}

.page {
    padding: 15px;
    margin: 0 auto;
    max-width: 1170px;
    color: rgb(60, 60, 60);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-color: rgb(255, 255, 255);

    .page__title {
        font-family: "Oswald";
        font-size: 30px;
        width: 100%;
        text-align: center;
        margin-bottom: 15px;
        margin-top: 20px;
    }

    .error {
        padding-top: 20px;
        width: 100%;
        text-align: center;

        &:first-letter {
            text-transform: capitalize;
        }
    }

    .form-search {
        width: 100%;

        form {
            max-height: 65px;
            height: 65px;
            transition: 0.2s;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: flex-start;
        }

        .form-search__input {
            box-sizing: border-box;
            border: none;
            height: 100%;
            transition: 0.5s;
            flex-grow: 1;
            background-color: rgb(255, 255, 255);
            border: 1px solid lightgrey;
            outline: none;
            margin-right: 15px;
            padding-left: 15px;
        }

        .form-search__submit {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 140px;
            font-size: 16px;
            height: 100%;
            font-family: "Montserrat";
            text-transform: uppercase;
            transition: 0.5s;
            color: rgb(59, 59, 59);
            font-weight: bold;
            background-color: #303030;
            color: #fff;
            border: none;
            outline: none;
        }
    }

    .spinner {
        width: 50px;
        height: 50px;
        margin: 0px;
        border: 5px solid #303030;
        border-left: 5px solid grey;
        animation: spin 1s infinite linear;
        border-radius: 100%;
    }

    .city-weather {
        flex-grow: 1;
        width: 100%;
        display: flex;
        flex-direction: column;
        font-family: "Montserrat";

        .city-weather__today {
            text-align: center;
            margin-top: 20px;
            margin-bottom: 20px;
            border: 2px solid #303030;
            //box-shadow: 0px 0px 3px 1px darkgrey;
            border-radius: 4px;

            .city-weather__today-date {
                font-size: 30px;
                margin: 15px 0px;
                span {
                    color: red;
                }
            }

            .city-weather__today-temp {
                font-size: 36px;
                margin: 15px 0px;
            }
        }

        .city-weather__daily-cards {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            grid-template-rows: 1fr 1fr;
            grid-gap: 10px;

            .city-weather__daily-card {
                display: flex;
                flex-direction: column;
                align-items: center;
                height: 235px;
                text-align: center;
            }

            .card {
                .card__top-date {
                    margin-top: 10px;
                    font-size: 20px;

                    span {
                        color: red;
                    }
                }

                .card__top {
                    width: 100%;
                    border: 2px solid #303030;
                    border-bottom: 0px;
                }

                .card__top-temp {
                    font-size: 40px;
                    margin: 10px 0px;
                }

                .card__body {
                    background-color: #303030;
                    color: #fff;
                    width: 100%;
                    height: 100%;
                    display: grid;
                    grid-template-columns: repeat(2, 1fr);

                    .card__body-item {
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        font-size: 18px;

                        span {
                            &:first-child {
                                font-size: 15px;
                            }
                        }
                    }
                }
            }
        }
    }
}

footer {
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    min-height: 80px;
    color: #fff;
    background-color: rgb(80, 80, 80);

    a {
        margin-left: 5px;
        align-items: center;
        display: flex;
        color: #fff;
    }

    img {
        height: 21px;
    }
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

@media screen and (min-width: 1170px) {
    .page {
        width: 1170px;
    }
}

@media screen and (max-width: 1170px) {
    .page {
        width: 100%;
        max-width: 100%;
    }
}

@media screen and (max-width: 600px) {
    .page {
        .city-weather {
            .city-weather__daily-cards {
                grid-template-columns: repeat(2, 1fr);
            }
        }
    }
}
